@import 'Common';

.tabBar{
  li{
    &:nth-of-type(3){
      a{
        color: #513736;
      }
    }
  }
}
main{
  height: 85.8%;
  padding-top: 64px;
  .findMethod{
    display: flex;
    flex-direction: row;
    align-items: center;
    height: 80px;
    background: #eee;
    li{
      overflow: hidden;
      flex: 1;
      a{
        display: flex;
        flex-direction: column;
        align-items: center;
        color: #513736;
        transition: transform 300ms linear 0s;
        &:hover{
          transform: scale(1.05);
        }
        svg{
          font-size: 22px;
          margin: 5px 0;
        }
      }
    }
  }
  .recommendSection{
    header{
      height: 22px;
      line-height: 22px;
      text-indent: 15px;
      color: #8e9399;
    }
    li{
      height: 54px;
      line-height: 54px;
      border-top: 1px solid #ebebeb;
      border-bottom: 1px solid #ebebeb;
      text-align: center;
      color: #8e9399;
      font-size: 14px;
    }
  }
}