.searchBox{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  height: 7.3%;
  min-height: 53px;
  padding-top: 64px;
  .searchInner{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 88%;
    height: 50%;
    background-color: #f1f2f4;
    border-radius: 5px;
    svg{
      font-size: 16px;
      color: #bebcc1;
      align-self: center;
      margin-left: 15px;
    }
    input{
      flex: 1;
      background: transparent;
      border: none;
      padding: 0 15px;
      &::placeholder{
        text-align: center;
      }
    }
  }
}